.navbar__link a {
  position: relative;
}
.navbar__link a::after {
  content: "";
  bottom: 0;
  height: 2px;
  left: 50%;
  opacity: 0;
  position: absolute;
  transform: translate(-50%, 0);
  transition: opacity 0.3s ease, transform 0.3s ease;
  width: 90%;
}
.navbar__link a:hover::after, .navbar__link a:focus-visible::after {
  opacity: 1;
  transform: translate(-50%, 0.25rem);
}

.hero, .content {
  padding-left: clamp(calc(24 / 16)rem, 6.2vw, calc(80 / 16)rem);
  padding-right: clamp(calc(24 / 16)rem, 6.2vw, calc(80 / 16)rem);
}

.hero, .content {
  align-items: center;
  display: flex;
  flex-direction: column-reverse;
  justify-content: center;
}

html {
  box-sizing: border-box;
  font-size: 100%;
}

ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  min-height: 100vh;
}

body,
h1,
h2,
h3,
h4,
p {
  margin: 0;
}

a {
  outline: none;
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
}

input,
button,
textarea {
  border: none;
  font: inherit;
  outline: none;
}

button {
  color: inherit;
  cursor: pointer;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

@media (prefers-reduce-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

body {
  font-family: "Open Sans", sans-serif;
}

h1,
h2,
h3,
h4 {
  font-family: "Raleway", sans-serif;
  font-weight: 700;
}

nav > * {
  font-family: "Raleway", sans-serif;
}

header {
  padding-top: clamp(1.5rem, 4.7vw, 4rem);
  padding-left: clamp(1.5rem, 6vw, 5rem);
  padding-right: clamp(1.5rem, 6vw, 5rem);
}
header.navbar {
  font-size: clamp(calc(14 / 16)rem, 3vw, calc(17 / 16)rem);
  justify-content: space-between;
}

h1 {
  font-size: clamp(calc(26 / 16)rem, 3vw, calc(40 / 16)rem);
  line-height: 1.6;
  margin-bottom: calc(30 / 16)rem;
}

p {
  font-family: "Open Sans", sans-serif;
  line-height: 1.5;
}

@media (min-width: 59em) {
  h1 {
    line-height: 1.5;
    margin-bottom: 1.5rem;
  }
}
.form {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(calc(260 / 16)rem, 1fr));
}
.form__holder {
  position: relative;
  text-align: left;
}
.form__holder .form__input {
  border-radius: 0.25rem;
  border: 1px solid #070439;
  font-size: clamp(calc(13 / 16)rem, 1.2vw, calc(15 / 16)rem);
  padding: calc(14 / 16)rem 1.5rem;
  width: 100%;
}
.form__holder .form__input:focus-visible {
  box-shadow: 0 0 1px 2px #FFFFFF, 0 0 1px 4px #3c9f8f;
  border-color: transparent;
}
.form__holder .form__input.error {
  border-color: #c23359;
}
.form__holder .form__input.error + .form__error {
  visibility: visible;
}
.form__holder .form__input--blue {
  border-color: transparent;
}
.form__holder .form__input--blue:focus-visible {
  box-shadow: 0 0 1px 2px #585989, 0 0 1px 4px #FFFFFF;
  border-color: transparent;
}
.form__holder .form__input--blue.error + .form__error {
  color: #FFFFFF;
}
.form__holder button {
  width: 100%;
}
.form__error {
  color: #c23359;
  display: block;
  font-size: calc(12 / 16)rem;
  visibility: hidden;
}

.btn {
  border-radius: 0.25rem;
  font-size: calc(15 / 16)rem;
  font-family: "Raleway", sans-serif;
  line-height: 0;
}
.btn__cta {
  font-size: clamp(calc(11 / 16)rem, 2vw, calc(14 / 16)rem);
  height: clamp(calc(34 / 16)rem, 5vw, calc(48 / 16)rem);
}
.btn--default {
  background-color: #3065f8;
  color: #FFFFFF;
  font-weight: 700;
  height: 3rem;
  transition: background-color 0.3s ease;
}
.btn--default:hover {
  background-color: #6385e3;
}
.btn--default:focus-visible {
  box-shadow: 0 0 1px 2px #FFFFFF, 0 0 1px 4px #3c9f8f;
}
.btn--blue:focus-visible {
  box-shadow: 0 0 1px 2px #585989, 0 0 1px 4px #FFFFFF;
}

@media (min-width: 59em) {
  .form {
    grid-template-columns: minmax(50%, calc(320 / 16)rem) minmax(25%, calc(200 / 16)rem);
  }
  .form__holder:last-of-type {
    margin-left: 1rem;
  }
}
.email-success {
  background-color: #3065f8;
  border-radius: 0 0.25rem 0.25rem 0;
  color: #FFFFFF;
  font-size: 1rem;
  font-weight: 700;
  left: 0;
  padding: 2rem 2.5rem;
  position: fixed;
  transition: transform 0.3s ease;
  transform: translateX(-100%);
  top: 2rem;
}
.email-success.show {
  transform: translateX(0);
}

.navbar__logo {
  width: clamp(calc(86 / 16)rem, 13vw, calc(166 / 16)rem);
}
.navbar--flattened, .quote__wrapper, .content__link a, .content__link {
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}
.navbar--flattened .navbar__link a::after, .quote__wrapper .navbar__link a::after, .content__link .navbar__link a::after {
  background-color: #3c9f8f;
}
.navbar--flattened .navbar__link:not(:last-of-type), .quote__wrapper .navbar__link:not(:last-of-type), .content__link .navbar__link:not(:last-of-type) {
  margin-right: calc(26 / 16)rem;
}

.hero {
  color: #070439;
  margin-bottom: calc(96 / 16)rem;
  padding-top: calc(90 / 16)rem;
}
.hero__divider {
  flex: 0 1 100%;
  padding: 0 calc(10 / 16)rem;
  text-align: center;
}
.hero__divider:last-of-type {
  margin-bottom: calc(46 / 16)rem;
}
.hero__divider p {
  font-size: clamp(calc(15 / 16)rem, 3vw, calc(18 / 16)rem);
  margin-bottom: calc(34 / 16)rem;
}
.hero__image {
  width: clamp(calc(324 / 16)rem, 47vw, calc(638 / 16)rem);
}

.content {
  background: url("../images/bg-curve-mobile.svg") no-repeat center top;
  background-size: cover;
  padding-top: clamp(calc(100 / 16)rem, 12vw, calc(178 / 16)rem);
  padding-bottom: clamp(calc(87 / 16)rem, 8.4vw, calc(108 / 16)rem);
}
.content__divider {
  flex: 0 1 100%;
}
.content__divider > img {
  width: clamp(calc(338 / 16)rem, 42vw, calc(540 / 16)rem);
}
.content__divider:last-of-type {
  margin-bottom: calc(98 / 16)rem;
}
.content__divider > p {
  color: #585989;
  font-size: clamp(calc(15 / 16)rem, 2vw, calc(16 / 16)rem);
}
.content__divider > p:first-of-type {
  margin-bottom: calc(20 / 16)rem;
}
.content__divider > p:last-of-type {
  margin-bottom: calc(39 / 16)rem;
}
.content__title {
  font-size: clamp(calc(16 / 16)rem, 3vw, calc(40 / 16)rem);
  margin-bottom: calc(30 / 16)rem;
  text-align: center;
}
.content__link {
  justify-content: center;
  margin-bottom: calc(41 / 16)rem;
}
.content__link a {
  border-bottom: 2px solid #3c9f8f;
  color: #3c9f8f;
  font-size: clamp(calc(13 / 16)rem, 2vw, calc(16 / 16)rem);
  padding-bottom: 0.5rem;
  transition: color 0.3s ease, border-color 0.3s ease;
}
.content__link a svg {
  margin-left: 0.25rem;
  transition: transform 0.3s ease;
}
.content__link a svg use {
  transition: fill 0.3s ease;
}
.content__link a:hover {
  border-color: #75ccbe;
  color: #75ccbe;
}
.content__link a:hover svg {
  transform: translateX(25%);
}
.content__link a:hover svg use {
  fill: #75ccbe;
}
.content__link a:focus-visible {
  box-shadow: 0 0 1px 2px #FFFFFF, 0 0 1px 4px #3c9f8f;
}

.quote {
  box-shadow: 0 0 0.25rem 0.3rem rgba(163, 163, 163, 0.1);
  border-radius: 0.25rem;
  background: url("../images/icon-quotes.svg") no-repeat calc(28 / 16)rem calc(22 / 16)rem;
  margin: 0 auto;
  padding: calc(44 / 16)rem calc(26 / 16)rem 1.5rem;
  width: clamp(calc(298 / 16)rem, 26.5vw, calc(356 / 16)rem);
}
.quote__text {
  color: #070439;
  font-size: clamp(calc(11 / 16)rem, 2vw, calc(13 / 16)rem);
  line-height: 1.7;
  margin-bottom: 1rem;
}
.quote__wrapper img {
  border-radius: 50%;
  margin-right: 0.5rem;
  width: clamp(calc(26 / 16)rem, 3vw, calc(30 / 16)rem);
}
.quote__info h3 {
  font-size: clamp(calc(10 / 16)rem, 2vw, calc(12 / 16)rem);
}
.quote__info p {
  font-size: clamp(calc(7 / 16)rem, 2vw, calc(9 / 16)rem);
}

.cta {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #585989;
  padding-top: clamp(calc(58 / 16)rem, 12vw, calc(105 / 16)rem);
  padding-right: clamp(calc(50 / 16)rem, 6vw, calc(80 / 16)rem);
  padding-bottom: clamp(calc(48 / 16)rem, 12vw, calc(52 / 16)rem);
  padding-left: clamp(calc(50 / 16)rem, 6vw, calc(80 / 16)rem);
}
.cta__divider {
  margin-bottom: calc(28 / 16)rem;
  width: 100%;
}
.cta__divider:first-of-type {
  text-align: center;
}
.cta__title {
  color: #FFFFFF;
  font-size: clamp(calc(19 / 16)rem, 3.5vw, calc(32 / 16)rem);
  margin-bottom: calc(21 / 16)rem;
}
.cta__text {
  color: #e0e0e0;
  font-size: clamp(calc(15 / 16)rem, 2vw, calc(17 / 16)rem);
}

.footer {
  background-color: #070439;
  font-size: calc(17 / 16)rem;
  padding-top: clamp(calc(69 / 16)rem, 7.2vw, calc(92 / 16)rem);
  padding-right: clamp(calc(38 / 16)rem, 6.2vw, calc(80 / 16)rem);
  padding-bottom: clamp(calc(50 / 16)rem, 7.2vw, calc(88 / 16)rem);
  padding-left: clamp(calc(38 / 16)rem, 6.2vw, calc(80 / 16)rem);
}
.footer * {
  color: #dbdbdb;
}
.footer__brand {
  margin-bottom: calc(46 / 16)rem;
}
.footer__nav .footer__wrapper > p:first-of-type {
  margin-bottom: calc(24 / 16)rem;
}
.footer__nav .footer__wrapper > p svg {
  margin-right: calc(20 / 16)rem;
}
.footer__nav .footer__wrapper:not(:last-of-type) {
  margin-bottom: calc(52 / 16)rem;
}
.footer__nav .footer__wrapper:not(:last-of-type) li:not(:last-of-type) {
  margin-bottom: calc(24 / 16)rem;
}
.footer__nav .footer__wrapper a {
  transition: color 0.3s ease;
}
.footer__nav .footer__wrapper a:hover {
  color: #7F8DD2;
}
.footer__nav .footer__wrapper a::after {
  background-color: #dbdbdb;
}
.footer__nav .footer__wrapper:last-of-type {
  justify-content: center;
}
.footer__nav .footer__wrapper:last-of-type li:not(:last-of-type) {
  margin-right: calc(11 / 16)rem;
}
.footer__nav .footer__wrapper:last-of-type a g {
  transition: stroke 0.3s ease;
}
.footer__nav .footer__wrapper:last-of-type a path {
  transition: fill 0.3s ease;
}
.footer__nav .footer__wrapper:last-of-type a:hover g {
  stroke: #7F8DD2;
}
.footer__nav .footer__wrapper:last-of-type a:hover path {
  fill: #7F8DD2;
}

@media (min-width: 59em) {
  .hero {
    flex-direction: row;
  }
  .hero__divider {
    text-align: left;
  }

  .content {
    flex-direction: row;
  }
  .content__divider > p {
    color: #070439;
    width: 80%;
  }
  .content__divider > p:first-of-type, .content__divider > p:last-of-type {
    margin-bottom: calc(18 / 16)rem;
  }
  .content__divider > img {
    margin-left: auto;
  }
  .content__title {
    text-align: left;
  }
  .content__link {
    justify-content: flex-start;
    margin-bottom: calc(44 / 16)rem;
  }

  .quote {
    margin: 0;
  }

  .cta {
    flex-direction: row;
    justify-content: space-between;
  }
  .cta__divider {
    width: 50%;
  }
  .cta__divider:first-of-type {
    text-align: left;
  }
  .cta__divider:last-of-type {
    max-width: calc(478 / 16)rem;
  }
  .cta__text {
    width: 90%;
  }
  .cta .form {
    grid-template-columns: calc(198 / 16)rem minmax(calc(239 / 16)rem, calc(478 / 16)rem);
    grid-template-rows: repeat(2, auto);
  }
  .cta .form__holder:first-of-type {
    grid-column: 1/3;
    margin-bottom: calc(14 / 16)rem;
  }
  .cta .form__holder:last-of-type {
    margin-left: 0;
    grid-column: 1/2;
    grid-row: 2/3;
  }
  .cta .form__input {
    max-width: calc(478 / 16)rem;
  }

  .footer__nav {
    align-items: flex-start;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 90%;
  }
}